import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PlayerInfoComponent } from '@scouting/private/player/components/player-info/player-info.component';
import { PLAYER_ROUTES } from '@scouting/private/player/player.routes';
import { PlayerStatsComponent } from '@scouting/private/player/components/player-stats/player-stats.component';

const routes: Routes = [
  {
    path: PLAYER_ROUTES.PLAYER_STATS_ROUTE,
    component: PlayerStatsComponent,
  },
  {
    path: PLAYER_ROUTES.PLAYER_DETAILS,
    component: PlayerInfoComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PlayerRoutingModule {}
