import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Location } from '@angular/common';
import { BehaviorSubject, Observable } from 'rxjs';
import {
  EntitiesUtils,
  ENTITIES_ENUM,
  EventsService,
  IEvent,
  IPagination,
  IPlayer,
  IPlayerRole,
  IPlayerStats,
  ITeam,
  LoadingService,
  MessagingService,
  PlayerService,
} from '@aft-core';
import { FIXTURE_MODULE_ROUTES, generateFullFixtureModuleRoute } from '@sw-ui-components/modules/fixture/fixture.routes';
import { ConfirmationService } from 'primeng/api';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'aft-scout-player',
  templateUrl: './player-info.component.html',
  styleUrls: ['./player-info.component.scss'],
})
export class PlayerInfoComponent implements OnInit {
  isLoading$: BehaviorSubject<boolean>;
  playerDetails$: BehaviorSubject<IPlayer | undefined>;
  playerStats$: BehaviorSubject<IPlayerStats | undefined>;
  pagination$: Observable<IPagination>;
  playerEvents$: BehaviorSubject<Array<IEvent>>;
  entitiesEnum = ENTITIES_ENUM;
  private playerId: number = 0;

  constructor(
    private _router: Router,
    private _route: ActivatedRoute,
    private _playerService: PlayerService,
    private _loadingService: LoadingService,
    private _location: Location,
    private _eventsService: EventsService,
    private _confirmationService: ConfirmationService,
    private _translateService: TranslateService,
    private _messagingService: MessagingService
  ) {
    this.playerDetails$ = this._playerService.playerDetails$;
    this.playerStats$ = this._playerService.playerStats$;
    this.isLoading$ = this._loadingService.loading$;
    this.pagination$ = this._eventsService.pagination$.asObservable();
    this.playerEvents$ = this._eventsService.events$;    
  }

  ngOnInit(): void {
    this._route.params.subscribe((params: Params) => {
      if (params.playerId) {
        const { playerDetailsParams } = this._playerService;

        const detailArray = [
          playerDetailsParams.CAREER,
          playerDetailsParams.TRANSFERS,
          playerDetailsParams.POSITIONS,
          playerDetailsParams.HIGHLIGHTS,
        ];
        this.playerId = params.playerId;
        this._playerService.getPlayerDetails(params.playerId, detailArray);
        this._playerService.getPlayerStats(params.playerId);
        this._eventsService.loadPlayerEvents(params.playerId);
      }
    });
  }

  onContactPlayer(): void {
    this._confirmationService.confirm({
      header: this._translateService.instant('PLAYER.CONTACT.MODAL_HEADER'),
      message: this._translateService.instant('PLAYER.CONTACT.MODAL_BODY'),
      acceptLabel: this._translateService.instant('PLAYER.CONTACT.MODAL_ACCEPT'),
      rejectLabel: this._translateService.instant('GENERAL.CANCEL'),
      accept: () => {
        const playerDetails = this.playerDetails$.value;
        if (playerDetails) {
          this._playerService.playerContactInfo(
            playerDetails.firstName,
            playerDetails.lastName,
            playerDetails.currentTeam?.name || 'No team'
          );
        } else {
          this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
        }
      },
    });
  }

  getPlayerRole(playerRole: number | undefined): string {
    const roleId = playerRole || this.playerDetails$.value?.roleId;

    const role: IPlayerRole | undefined = EntitiesUtils.findRoleById(roleId);

    return role?.code3 || 'Not found';
  }

  goBack(): void {
    this._location.back();
  }

  goToTeam(team: ITeam | undefined): void {
    if (team) {
      this._router.navigate(generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.TEAM_DETAILS, team.id));
    }
  }

  pageChanged($event: any): void {
    this._eventsService.loadPlayerEvents(this.playerId, $event.page + 1);
  }
}
