import { Component, OnDestroy, OnInit } from '@angular/core';
import { IPlayer, PLAYER_HIGHLIGHTS_ARRAY, PlayerService } from '@aft-core';
import { BehaviorSubject, Subscription } from 'rxjs';

type TPlayerHighlight = {
  label: string;
  value: number;
  image: string;
};

@Component({
  selector: 'aft-scout-player-highlights',
  templateUrl: './player-highlights.component.html',
  styleUrls: ['./player-highlights.component.scss'],
})
export class PlayerHighlightsComponent implements OnInit, OnDestroy {
  playerDetails$: BehaviorSubject<IPlayer | undefined>;

  playerHighlights: Array<TPlayerHighlight> = [];
  private _subscriptions: Subscription[] = [];

  constructor(private _playerService: PlayerService) {
    this.playerDetails$ = this._playerService.playerDetails$;
  }

  ngOnInit(): void {
    this._subscriptions.push(
      this.playerDetails$.subscribe(player => {
        if (player) {
          this.playerHighlights = [];

          const { highlights, roleId } = player;
          // @ts-ignore
          const positionHighlights = PLAYER_HIGHLIGHTS_ARRAY[roleId] as Array<string>;

          if (highlights) {
            positionHighlights.forEach(stat => {
              // @ts-ignore
              const statValue = highlights[stat];

              this.playerHighlights.push({
                label: `STATS_WYSCOUT.${stat.toUpperCase()}`,
                value: statValue,
                image: `assets/images/highlights/${stat}.jpg`,
              });
            });
          }
        }
      })
    );
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(x => x.unsubscribe());
  }
}
