// eslint-disable-next-line @typescript-eslint/naming-convention
import { APP_ROUTES } from '@aft-core';

// eslint-disable-next-line @typescript-eslint/naming-convention
export const PLAYER_ROUTES = {
  MODULE_ROUTE: 'player',
  PLAYER_DETAILS: ':playerId',
  PLAYER_STATS_ROUTE: 'stats/:playerId',
  PLAYER_STATS: 'stats',
};

export const generatePlayerModuleRoute = (
  route: string,
  param: string | undefined,
  queryParams?: any | undefined
): Array<string> => {
  const routeArray = [...APP_ROUTES.PRIVATE, PLAYER_ROUTES.MODULE_ROUTE, route];
  if (param) {
    routeArray.push(param);
  }
  if (queryParams) {
    routeArray.push(queryParams);
  }
  return routeArray;
};
